import React from "react";
import { Form, Input, Select } from "antd";

const { TextArea } = Input;

class ServQuesForm extends React.Component {
  state = {
    sel: this.props.sel
  };

  render() {
    const { getFieldDecorator } = this.props.form;
    const { sel } = this.state;

    return (
      <div>
        <Form>
          <Form.Item label=''>
            {
              getFieldDecorator("title", {
                initialValue: sel && sel.title
              })(
                <Input disabled={true}/>
              )
            }
          </Form.Item>

          <Form.Item label=''>
            {
              getFieldDecorator("keyword", {
                initialValue: (sel && sel.keyword) ? sel.keyword.split(" ") : []
              })(
                <Select mode="tags" placeholder="">
                </Select>
              )
            }
          </Form.Item>

          <Form.Item label=''>
            {
              getFieldDecorator("des", {
                initialValue: sel && sel.des
              })(
                <TextArea
                  autosize={{minRows: 2, maxRows: 6}}
                />
              )
            }
          </Form.Item>
        </Form>
      </div>
    );
  }
}

export default Form.create({})(ServQuesForm);
